import React from 'react'

export default function Copyright() {

    const currentYear = new Date().getFullYear()

    return (

        <React.Fragment>

            <div className="copy">
                &copy; { currentYear } &nbsp;
                <a href="http://www.linkedin.com/in/anderson-isaac" target="_blank" rel="noreferrer">
                    Anderson Isaac Rodrigues
                </a>
            </div>

        </React.Fragment>

    )

}