import React from 'react'

export default function Habilidades() {

    return (

        <React.Fragment>

            <section id="habilidades" className="home-section">
                <div className="container">
                    <div className="col-xs-12">
                        <h3 className="title wow slideInLeft">
                            <span className="number"><i>3</i></span> Habilidades
                        </h3>
                    </div>
                    <div className="content">
                        <div id="hb-list" className="row">
                            <div className="col-xs-12 col-sm-6 col-md-4 wow fadeInUp">
                                <h4 className="box-title">Front-end</h4>
                                <ul className="hb-list">
                                    <li>HTML5</li>
                                    <li>CSS3</li>
                                    <li>SASS | LESS</li>
                                    <li>UI | UX</li>
                                    <li>Material Design</li>
                                    <li>Javascript | jQuery</li>
                                    <li>React</li>            
                                </ul>
                            </div>
                            <div className="col-xs-12 col-sm-6 col-md-4 wow fadeInUp" data-wow-delay="300ms">
                                <h4 className="box-title">Back-end</h4>
                                <ul className="hb-list">
                                    <li>PHP</li>
                                    <li>Laravel</li>
                                    <li>MySQL</li>             
                                    <li>Wordpress</li>
                                    <li>Opencart</li>
                                </ul>
                            </div>
                            <div className="col-xs-12 col-sm-6 col-md-4 wow fadeInUp" data-wow-delay="600ms">
                                <h4 className="box-title">Outros</h4>
                                <ul className="hb-list">
                                    <li>GIT</li>
                                    <li>Design Patterns</li>
                                    <li>POO + MVC</li>
                                    <li>Testes de software</li>
                                    <li>Adobe Photoshop + Illustrator</li>                
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </React.Fragment>
    )

}